	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"This Is Not A Replica","date":"21 Oct 2005","slug":"/2005-10-21-this-is-not-a-replica/","tags":"Y2005"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/54599089/\" title=\"World Cup\"><img src=\"http://static.flickr.com/26/54599089_6fbdd4c54c.jpg\" width=\"375\" height=\"500\" alt=\"worldcup\" /></a></p>\n<p>While Christopher and I were catching colds in Germany this week, Mama was in southern Italy, where she got to hold the World Cup. Yes, <a href=\"http://fifaworldcup.yahoo.com/\">that</a> World Cup.</p>"},"next":{"fields":{"title":"Credit Cards in Germany","date":"21 Oct 2005","slug":"/2005-10-21-credit-cards-in-germany/"}},"previous":{"fields":{"title":"Dragon Hunters","date":"22 Oct 2005","slug":"/2005-10-22-dragon-hunters/"}}},{"node":{"fields":{"title":"Credit Cards in Germany","date":"21 Oct 2005","slug":"/2005-10-21-credit-cards-in-germany/","tags":"Y2005"},"html":"<p><a href=\"http://andrewhammel.typepad.com/german_joys/2005/10/credit_cards_ho.html\" title=\"German Joys: Credit Cards, Honor, and the Fatherland\">German Joys</a> notes a <a href=\"http://www.csmonitor.com/2005/1020/p06s02-woeu.htm\" title=\"Credit-card companies woo few Germans | csmonitor.com\">story in the Christian Science Monitor</a> on the lack of acceptance of credit cards in Germany compared to other countries in Europe. It included this strange quote from a \"high school student\":</p>\n<blockquote><p>When I want something I pay for it myself. It's a question of honor.</p></blockquote>\n<p>The article tries to make the point that the \"German mentality doesn't get into debt\", but at the end notes that consumer bankruptcies are on the rise. They must be getting into debt somehow, probably with overdraft lines of credit at their banks and purchases from catalog merchants like <a href=\"http://www.otto.de/\">Otto</a> and <a href=\"http://www.quelle.de/\">Quelle</a>. Bank cards are also widely accepted, and are cheaper than credit cards for both the merchant and the consumer. They also tie in nicely with your overdraft protection. So why pay extra to get and use a credit card?</p>\n<p>We tend to use our credit cards a lot, but only to collect frequent flyer miles from Lufthansa.</p>"},"next":{"fields":{"title":"High School","date":"18 Oct 2005","slug":"/2005-10-18-high-school/"}},"previous":{"fields":{"title":"This Is Not A Replica","date":"21 Oct 2005","slug":"/2005-10-21-this-is-not-a-replica/"}}},{"node":{"fields":{"title":"High School","date":"18 Oct 2005","slug":"/2005-10-18-high-school/","tags":"Y2005"},"html":"<p>I graduated in 1980. Jimmy Carter was still President.</p>\n<p>My hometown is exactly 1 mile square. If in summer you stood in the middle of any street, you could see cornfields at both ends.</p>\n<p>In band I played bass clarinet. Nowadays I wish I had learned bass guitar.</p>\n<p>I was active in speech, debate and theatre. Our best play was \"Diary of Anne Frank\". I also enjoyed doing lights. </p>\n<p>Until my junior year I took the maximum number of classes possible. I then needed only one credit to graduate, and slacked off my senior year. The system was rigged; it was impossible to earn enough credits to graduate as a junior.</p>\n<p>I was an AFS exchange student to Brazil the summer after my junior year. My wife was an exchange student with AFS in Minnesota. We met because we were both wearing an AFS sweatshirt at a church youth function.</p>\n<p>I played tennis, but not very well.</p>\n<p>We had computer classes. Thanks to <a href=\"http://ldt.stanford.edu/ldt1999/Students/kemery/esc/otCompanyFrame.htm\" title=\"Minnesota Educational Computing Consortium\">MECC</a>, even our small high school had timeshare access to a mainframe in Minneapolis. At first we typed in BASIC programs on punch cards and ran them over an acoustic couple. Later we got an Apple II where we could work locally and play Breakout.</p>\n<p>My hometown lies on the 45th Parallel, exactly halfway between the North Pole and the Equator. It is also nearly equidistant from Minneapolis, Fargo and Sioux Falls.</p>\n<p>After my experience in Brazil, I wanted to study International Relations. I went to the University of Minnesota rather than a rural public or Lutheran college because they offered Portuguese as a foreign language. </p>\n<p>There were 88 students in my graduating class. Three classmates died before we graduated, one from leukemia, two in car crashes. I'm rather certain that affects me even today.</p>\n<p>I was in a car crash myself while driving to my wife's prom. I never made it; the car was totaled, I needed stitches. She found a new date that very evening.</p>\n<p>Our 25th class reunion was this summer. It was two weeks after our vacation in Minnesota, so I missed it.</p>"},"next":{"fields":{"title":"Jealousy","date":"17 Oct 2005","slug":"/2005-10-17-jealousy/"}},"previous":{"fields":{"title":"Credit Cards in Germany","date":"21 Oct 2005","slug":"/2005-10-21-credit-cards-in-germany/"}}}],"pathPrefix":"","first":false,"last":false,"index":271,"pageCount":1101}}

